.section-main {
	// height: 752px
	height: 100%;
	/*border: 1px solid red*/
	background-image: url('../img/section-main-bg.jpg');
	background-repeat: no-repeat;
	background-position: 9px 0;

	@include r(979) {
		background-position: center 0; } }

.header {
	height: 74px;
	/*border: 1px solid red*/
	background-image: url('../img/logo.png');
	background-repeat: no-repeat;
	background-position: 0 23px;

	@include r(979) {
		background-position: 5px 23px; }

	@include r(700) {
		background-position: 5px 5px; } }

.description-h-wrapper {
	padding: 30px 0 0 346px;
	/*border: 1px solid green*/

	@include r(979) {
		padding: 30px 0 0 0;
		float: right; }

	@include r(700) {
		padding: 18px 0 0 0; }

	@include r(575) {
		float: left;
		padding: 55px 0 0 5px; } }

.description-h {
	max-width: 100%;
	height: 37px;
	/*border: 1px solid red*/
	display: inline-block;
	margin-right: 26px;

	@include r(979) {
		margin-right: 5px; }

	// +r(700)
	// 	display: block

	&__img {
		float: left;

		@include r(700) {
			height: 25px; }

		@include r(575) {
			height: 15px; } }

	&__part1, &__part2 {
		display: inline-block;
		color: #ededed; }

	&__part1 {
		margin: 1px 0 0 8px;
		font-size: 1.5em;
		letter-spacing: 0.03em;

		@include r(700) {
			font-size: 1em; }

		@include r(575) {
			font-size: 0.55em; } }

	&__part2 {
		margin-left: 1px;
		font-size: 1.1em;
		letter-spacing: 0.01em;

		@include r(700) {
			font-size: 0.75em; }

		@include r(575) {
			font-size: 0.55em; } } }

.title {
	font-family: 'Roboto Condensed', sans-serif;
	font-size: 3.72em;
	color: #ededed;
	text-transform: uppercase;
	letter-spacing: 0.001em;
	margin-top: 18px;
	background: linear-gradient(to right, #f0e394 0%, #b49b52 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;

	@include r(979) {
		font-size: 3em; }

	@include r(780) {
		font-size: 2.5em; }

	@include r(650) {
		font-size: 2em; } }

.main-wrapper {
	display: flex;
	/*justify-content: space-between*/
	margin-top: 9px;

	@include r(810) {
		flex-wrap: wrap;
		justify-content: space-between; }

	@include r(650) {
		margin-top: 0; }

	@include r(569) {
		justify-content: center; } }

.description, .discount, .order-form {
	height: 555px;
	color: #ffffff;
	display: inline-block; }

.description {
	// background-image: url('../img/bottle.png')
	// background-repeat: no-repeat
	// background-position: -3px 243px
	max-width: 372px;
	/*border: 1px solid red*/

	&__title {
		font-family: 'Roboto', sans-serif;
		font-size: 1.27em;
		line-height: 1.33em;
		letter-spacing: 0.07em;
		max-width: 340px; }

	&__text {
		margin-top: 18px; }

	&__item {
		background-image: url('../img/check-mark.png');
		background-repeat: no-repeat;
		height: 24px;
		padding-left: 35px;
		margin-bottom: 13px;
		// -webkit-border-image: -webkit-gradient(linear, 0 0, 0 100%, from(red), to(rgba(0, 0, 0, 0))) 100% 1
		// border-bottom: 1px solid
		position: relative;

		&:nth-child(4) {
				position: inherit; }


		&:after {
			position: absolute;
			content: '';
			bottom: -6px;
			left: -25px;
			width: 266px;
			height: 1px;
			background-image: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.35), rgba(255, 255, 255, 0)); } }

	&__img {
		// width: 100%
		margin: 23px 0px 0px -3px;

		@include r(569) {
			width: 100%; } } }

.discount {
	/*border: 1px solid green*/
	// display: flex
	// align-items: flex-end
	// background-image: url('../img/50.png')
	// background-repeat: no-repeat
	// background-position: 0 351px
	width: 280px;

	@include r(810) {
		display: flex;
		justify-content: space-between;
		align-items: center;
		order: 1;
		width: 100%;
		height: 100%; }

	@include r(661) {
		flex-direction: column;
		width: min-content; }

