			margin-top: 0; } }

	&__text {
		margin-top: 8px;
		text-align: center;
		font-family: 'PT Sans Narrow', sans-serif;
		font-size: 1.1em;
		letter-spacing: 0.05em;
		word-spacing: -0.1em;

		@include r(810) {
			margin-top: 0;
			font-size: 1.5em; } } }

.order-form {
	/*border: 1px solid blue*/
	width: 290px;
	margin-left: 38px;

	@include r(700) {
		margin-left: 0; }

	@include r(661) {
		height: 100%; } }

.timer {
	display: flex;
	margin: 5px 0 0 -1px;
	position: relative;

	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 47px;
		height: 54px;
		background: #f6f6f6;
		border: #ffffff;
		border-radius: 5px;
		margin-right: 5px;
		position: relative;

		&:after {
			position: absolute;
			content: '';
			top: 27px;
			width: 45px;
			height: 1px;
			background: #cecece; }

		&:nth-child(1) {
			opacity: 0.7; } }

	&__number {
		color: #000000;
		font-family: 'PT Sans Narrow', sans-serif;
		font-weight: 700;
		font-size: 3.1em; }

	&__text {
		position: absolute;
		content: '';
		top: 7px;
		right: 3px;
		font-family: 'PT Sans Narrow', sans-serif;
		font-size: 1.1em;
		line-height: 1.1em;
		letter-spacing: 0.04em; } }

.form-wrapper {
	width: 290px;
	height: 410px;
	margin: 30px 0 0 0;
	background: #ffffff;
	border-radius: 10px;
	background-image: url('../img/form-wrapper-bg.jpg');
	background-repeat: no-repeat;
	position: relative;
