		background-image: url('../img/shine.png'); }
	// 	border-radius: 50%
 }	// 	background: linear-gradient(167deg, #ad3a18 40%, #431309 53%)

.price {
	// border: 1px solid blue
	// width: 300px
	padding: 18px 0 0 25px; }

.old-price {
	display: inline-block;
	margin-right: 21px;

	&__title {
		font-family: 'PT Sans Narrow', sans-serif;
