.form-p {
	padding-top: 40px;

	&__input {
		width: 255px;
		height: 55px;
		border: 1px solid #e1e1e1;
		border-radius: 5px;
		margin: 0 0 15px 17px;
		background: #f6f6f6;
		padding: 0 0 5px 14px;
		color: #000000;
		font-family: 'Open Sans', sans-serif;
		font-size: 0.879em;

		&::placeholder {
			color: #929292; } }

	&__button {
		width: 255px;
		height: 60px;
		border-radius: 5px;
		margin: 10px 0 0 17px;
		background: linear-gradient(to bottom, #fa4a1b 0%, #d73f17 100%);
		color: #ffffff;
		font-family: 'PT Sans Narrow', sans-serif;
		font-size: 1.35em;
		font-weight: 700;
		text-transform: uppercase;
		padding: 0 0 2px 42px;
		font-weight: 700;
		letter-spacing: 0.01em;
		position: relative;

		&:before {
			position: absolute;
			content: '';
			top: 0px;
			left: 0px;
			width: 51px;
			height: 60px;
			/*background: #b13412*/
			background-image: url('../img/bracket.png');
			background-repeat: no-repeat;
			background-position: center center;
			background-color: #b13412;
			border-radius: 5px 0 0 5px; } } }

.security {
	height: 17px;
	background-image: url('../img/lock.png');
